import { useState } from "react"
import '../style.css'

const Controller = ({ currentLocation, setCurrentLocation }) => {
    const [location, setLocation] = useState(currentLocation)

    const handleChange = (e) => {
        console.log(e.target.value)
        setLocation(e.target.value) // 
        setCurrentLocation( e.target.value ) // location  state
    }

    return(
        <div>
            <select value={location} onChange={handleChange} className="Dropdown">
                <option value="">Kaohsiung</option>
                <option value="">Taipei</option>
                <option value="">Taichung</option>
                <option value="">Chaiyi</option>
            </select>
        </div>
    )
}
export default Controller